.pipboy-about-main {
  width: 100vw;
  min-height: 0;
  height: 100%;
  background: transparent;
  display: flex;
  align-items: stretch;
  justify-content: stretch; /* changed from center */
  overflow-y: auto;
  overflow-x: hidden;
  flex: 1 1 auto;
  box-sizing: border-box;
  padding-bottom: 2rem;
}

.pipboy-about-grid {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  align-items: start;
  justify-items: stretch; /* changed from start */
  width: 100vw;
  max-width: 100vw;       /* changed from 1200px */
  margin: 0;
  border-radius: 0;
  box-shadow: none;
  overflow: visible;
  padding: 0 2vw;
  gap: 0;
}

.pipboy-about-left,
.pipboy-about-right {
  min-width: 0;
  min-height: 0;
  box-sizing: border-box;
  padding: 3vw 3vw;
  display: flex;
  flex-direction: column;
}

.pipboy-about-left {
  align-items: flex-start;
  justify-content: flex-start;
}

.pipboy-about-right {
  align-items: flex-start; 
  justify-content: flex-start;
  padding-top: 0;
}

.pipboy-name-bar {
  font-size: clamp(1.2rem, 5vw, 2rem);
  padding: 0.5rem 1rem;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 1.2rem;
  background: var(--primary-color);
  color: #222;
  font-weight: bold;
  border-radius: 2px;
  font-family: "VT323", monospace;
  box-shadow: 0 0 12px var(--primary-color);
}

.pipboy-skill-list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.pipboy-skill-list li {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  margin-bottom: 1rem;
  gap: 1.5rem;
  cursor: pointer;
  font-family: "VT323", monospace;
  color: var(--primary-color);
  border-radius: 4px;
  padding: 0.2rem 0.5rem;
  outline: none;
  background: none;
  transition: background 0.15s, color 0.15s;
  white-space: nowrap;
}
.pipboy-skill-list li.selected,
.pipboy-skill-list li:focus,
.pipboy-skill-list li:hover {
  background: var(--primary-color);
  color: #222;
}


.pipboy-skill-accordion {
  display: none;
}

@media (max-width: 900px) {
   .pipboy-about-main {
    padding-top: 4.5rem;
    padding-bottom: 1.5rem;
    height: auto;
    min-height: 100vh;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  .pipboy-about-grid {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    width: 100vw;
    max-width: 100vw;
    padding: 0 2vw;
    gap: 0;
    justify-items: center;
  }
  .pipboy-about-left {
    align-items: stretch;
    width: 100%;
    padding: 4vw 2vw;
    display: flex;
    flex-direction: column;
  }
  .pipboy-about-right {
    display: none;
  }
  .pipboy-skill-list {
    width: 100%;
    padding: 0;
    margin: 0;
  }
  .pipboy-skill-list li {
    width: 100%;
    max-width: 100%;
    white-space: normal;
    word-break: break-word;
    overflow-wrap: anywhere;
    overflow: visible;
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
    box-sizing: border-box;
  }
  .pipboy-skill-accordion {
    display: block;
    width: 100%;
    margin: 0 0 1.5rem 0;
    padding: 0;
    background: none;
  }
  .pipboy-skill-accordion-content {
    padding: 0.5rem 0 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: none;
  }
  .vaultboy-img {
    max-width: 120px;
    margin-bottom: 0.7rem;
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto 1.2rem auto;
  }
  .pipboy-skill-detail {
    text-align: center;
    margin-top: 0.5rem;
    font-size: 1rem;
  }
}

@media (min-width: 901px) {
  .pipboy-about-grid {
    grid-template-columns: 1.2fr 1fr;
    gap: 2rem;
  }
  .pipboy-about-left {
    align-items: flex-start;
    padding: 3vw 3vw;
  }
  .pipboy-about-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 3vw 3vw;
  }
  .pipboy-skill-accordion {
    display: none;
  }
  .vaultboy-img {
    max-width: 180px;
  }
  .pipboy-skill-list li {
  white-space: normal;
  word-break: break-word;
  overflow-wrap: anywhere;
  max-width: 95%;
  padding: 0.2em 0.5em;
  margin-bottom: 0.4em;
  border-radius: 4px;
  transition: background 0.2s;
}

  .pipboy-skill-list li.selected {
    background: rgba(255, 213, 44, 0.12);
    border: 1.5px solid var(--primary-color, #ffd52c);
    color: var(--primary-color, #ffd52c);
    box-shadow: 0 0 8px #ffd52c55;
  }

  .pipboy-skill-name {
    display: block;
    font-size: 1.18rem;
    line-height: 1.3;
    font-family: 'Share Tech Mono', monospace;
    text-shadow: 0 0 6px var(--primary-color, #ffd52c);
  }
  .pipboy-skill-detail h2 {
  font-size: 2.1rem;
  line-height: 1.1;
  word-break: break-word;
  overflow-wrap: anywhere;
  margin-bottom: 0.7rem;
  text-shadow: 0 0 10px var(--primary-color, #ffd52c);
}
}
